.wrapper {
    position:relative;
    top:0;
    left:0;
    display:flex;
    justify-content: center;
    place-items: center;
    width:100vw;
    height:100vh;
    background-color: #4158D0;
    background-image: linear-gradient(43deg, #4158D0 0%, #C850C0 46%, #FFCC70 100%);
    
    background-size:200% 200%;

    perspective:1000px;
    
    @keyframes background-animate {
        0% {
            background-position:top right;
            background-filter:hue-rotate(0deg);
        }

        25% {
            background-position:top left;
        }

        50% {
            background-position:bottom left;
        }

        100% {
            background-position:bottom right;
            background-filter:hue-rotate(360deg);
        }
    }

    animation:background-animate 30s alternate infinite;

    .container {
        position:relative;
        transform:translateY(15px);
        //transform:rotateX(10deg);
        
        .gameContainer {
            overflow:hidden;
            display:inline-block;
            border-radius:inherit;
            position:absolute;
            width:inherit;
            height:inherit;
        }

        &.gameover::after {
            content:'';
            position:absolute;
            top:0;
            left:0;
            width:100%;
            height:100%;
            z-index:2;
            border-radius:inherit;
            background-image: radial-gradient(rgba(0, 0, 0, 0.452),rgba(0, 0, 0, 0.295));
            backdrop-filter:grayscale(1) blur(2px);

            @keyframes gameover-appear {
                from { opacity:0; }
                to { opacity:1; }
            }

            animation:gameover-appear 1s;
        }

        &.gameover::before {
            content:"Game Over!";
            position:absolute;
            top:40%;
            left:50%;
            transform:translate(-50%,-50%);
            font-size:3em;
            letter-spacing: 1em;
            white-space: nowrap;
            background-image:radial-gradient(rgb(255, 255, 255),rgb(238, 106, 106));
            background-clip:text;
            color:transparent;
            z-index:3;
            filter:drop-shadow(0 0 2px red);

            @keyframes gameover-animate-in {
                from {
                    letter-spacing: 1em;
                    transform:translate(-50%,-100%);
                    opacity:0.2;
                    filter:url(#glitch);
                }

                to {
                    letter-spacing:0.1em;
                    transform:translate(-50%,-50%);
                    opacity:1;
                }
            }

            animation:gameover-animate-in 2.5s cubic-bezier(0.87, 0, 0.13, 1) forwards;
        }
    }
}

.pauseContainer {
    position:absolute;
    top:-60px;
    right:0;
}

.scoreContainer {
    position:absolute;
    top:-60px;
    left:0;
    min-width:50px;
    height:50px;
    z-index:10;
}

.blockContainer {
    position:absolute;
    top:-60px;
    left:50%;
    transform:translateX(-50%);
    width:100px;
    height:100px;
    text-align:center;
}